import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { navItems } from '../../_nav';
import { environment } from '../../../environments/environment';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import * as $ from 'jquery';

@Component({
  selector: 'app-usuario-detalhe',
  templateUrl: './usuario-detalhe.component.html',
  styleUrls: ['./usuario-detalhe.component.scss']
})
export class UsuarioDetalheComponent implements OnInit {

  public form: FormGroup;

  public data: any;

  public userDto: any;

  public id: any;

  public showPassField = false;

  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;

  public tiposUsuario: any;

  constructor(@Inject(DOCUMENT) _document?: any, public router?: Router, public httpClient?: HttpClient, public formBuilder?: FormBuilder, private route?: ActivatedRoute) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ['class']
    });
  }


  ngOnDestroy(): void {
    this.changes.disconnect();
  }

  ngOnInit() {

    $(".spinner").show();
    $(".darken-bkg").show();

    this.data = this.retrieveStoredData();

    this.validaPerfil(this.data["cdTipo"]);

    this.carregarTiposUsuario();

    this.route.queryParams.subscribe(params => {
      this.id = params['id'];
      if (!this.id) {
        this.showPassField = true;
        $(".spinner").fadeOut();
        $(".darken-bkg").fadeOut();
      }
      else {
        this.carregarUsuario(this.id);

      }
    })

    this.form = this.formBuilder.group({
      cdLogin: null,
      nmUsuario: new FormControl("",[Validators.required]),
      dsLogin: new FormControl("",[Validators.required]),
      dsPassword: null,
      cdTipo: new FormControl("",[Validators.required]),
    })

    console.log(this.data);
    if (!this.data) {
      this.router.navigateByUrl("/403");
    }
  }

  retrieveStoredData() {
    return JSON.parse(localStorage.getItem("userData"));
  }

  carregarTiposUsuario() {
    this.httpClient.get(environment.API_ENDPOINT + "/usuario/tipos").subscribe((data) => {
      this.tiposUsuario = data;
    }, (error) => {
      console.log(error);
    })
  }

  carregarUsuario(id) {
    this.httpClient.get(environment.API_ENDPOINT + "/usuario/" + id + "?key=" + environment.KEY).subscribe((data) => {
      console.log(data);

      this.form.patchValue({
        cdLogin: data[0]['cdLogin'],
        nmUsuario: data[0]["nmUsuario"],
        dsLogin: data[0]["dsLogin"],
        cdTipo: data[0]["cdTipo"]
      })

      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      // this.form.value.nmUsuario = data[0]["nmUsuario"];
      // this.form.value.dsLogin = data[0]["dsLogin"];
      // this.form.value.dsTipo = data[0]["cdTipo"];

    })
  }

  salvar() {
    
    $(".spinner").show();
    $(".darken-bkg").show();
    
    console.log(this.form.value);

    var loginDto = this.form.value;
    loginDto.key = environment.KEY

    this.httpClient.post(environment.API_ENDPOINT + "/usuario/salvar", loginDto).subscribe((success) => {
      $(".alert-success").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
      setTimeout(() => {
        $(".alert-success").fadeOut();
        $(window).scrollTop(0);

      }, 3000)
    }, (error) => {
      console.log(error);
      $(window).scrollTop(0);
      $(".alert-danger").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 3000)
    })
  }

  logout() {
    localStorage.clear();
    this.router.navigateByUrl("/");
  }

  resetarSenha() {
    this.showPassField = true;
  }

  validaPerfil(cdTipo){
    if(cdTipo != 1){
      $(".usuarios").hide();
      window.location.href = "/#/403";
    }
  }

}
