import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { navItems } from '../../_nav';
import { environment } from '../../../environments/environment';
import * as $ from 'jquery';
import { FormBuilder, FormGroup } from '@angular/forms';
import { PerfilEnum } from '../../perfil.enum';
import { AlertComponent } from 'ngx-bootstrap';

@Component({
  selector: 'app-empresa-detalhe',
  templateUrl: './empresa-detalhe.component.html',
  styleUrls: ['./empresa-detalhe.component.scss']
})
export class EmpresaDetalheComponent implements OnInit {

  public Perfil = PerfilEnum;

  public data: any;
  public id: any;

  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;

  public enquadramentos: any;

  public presumido = false;
  public real = false;

  public empresa: any = [
    {
      stStatus: null
    }
  ];

  public estados: any;
  public cidades: any;

  public itensParcelamento = [];
  public parcelamentoOptions;

  public documento = {
    CSLL: false,
    COFINS: false,
    FGTS: false,
    GPS: false,
    ICMS: false,
    IRPJ: false,
    IRRF: false,
    ISS: false,
    PIS: false,
    ProcuradoriaFederal: false,
    SimplesNacional: false
  }

  public form: FormGroup;

  constructor(@Inject(DOCUMENT) _document?: any, public router?: Router, public httpClient?: HttpClient,
    public formBuider?: FormBuilder, public route?: ActivatedRoute) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ['class']
    });
  }


  ngOnDestroy(): void {
    this.changes.disconnect();
  }

  ngOnInit() {

    $(".spinner").show();
    $(".darken-bkg").show();

    this.form = this.formBuider.group({
      cdEmpresa: null,
      nmEmpresa: '',
      nmRazaoSocial: '',
      nrCnpj: '',
      nrInscricaoEstadual: '',
      cdEstado: '',
      cdCidade: '',
      nrInscricaoMunicipal: '',
      cdEnquadramentoFiscal: '',
      dtValCertificadoDigital: '',
      dtValAlvaraBombeiro: '',
      dtValAlvaraPrefeitura: '',
      dtValAlvaraSanitario: '',
      dtValCertReceita: '',
      dtValCertIcms: '',
      dtValCertFgts: '',
      dtValCertPrefeitura: '',
      dtValCertProcuradoriaIcms: '',
      dtValEnquadramentoEirelli: '',
      fgIsIrECsllTrimestral: '',
      dtJucesp: '',
      dsObservacaoJucesp: '',
      dsTipoCertificadoDigital: '',
      dsTipoDocumento: ''
    })

    this.carregarEnquadramentoFiscal();
    this.data = this.retrieveStoredData();
    console.log(this.data);
    if (!this.data) {
      this.router.navigateByUrl("/403");
    }

    this.validaPerfil(this.data["cdTipo"]);

    this.carregarEmpresa();

  }

  retrieveStoredData() {
    return JSON.parse(localStorage.getItem("userData"));
  }

  logout() {
    localStorage.clear();
    this.router.navigateByUrl("/");
  }

  carregarEnquadramentoFiscal() {
    this.httpClient.get(environment.API_ENDPOINT + "/empresa/enquadramento").subscribe((data) => {
      this.enquadramentos = data;
    })
  }

  gerenciarCamposByTipoEmpresa(cdEnquadramentoFiscal) {

    if (cdEnquadramentoFiscal == 2) {
      this.presumido = true;
      this.real = false;

      this.documento.PIS = true;
      this.documento.COFINS = true;
      this.documento.CSLL = true;
      this.documento.IRPJ = true;
      this.documento.ICMS = true;
      this.documento.ISS = true;

      this.documento.SimplesNacional = false;

    }
    else if (cdEnquadramentoFiscal == 3) {
      this.presumido = false;
      this.real = true;

      this.documento.PIS = true;
      this.documento.COFINS = true;
      this.documento.CSLL = true;
      this.documento.IRPJ = true;
      this.documento.ICMS = true;
      this.documento.ISS = true;

      this.documento.SimplesNacional = false;

    }
    else {
      this.presumido = false;
      this.real = false;

      this.documento.SimplesNacional = true;

      this.documento.PIS = false;
      this.documento.COFINS = false;
      this.documento.CSLL = false;
      this.documento.IRPJ = false;
      this.documento.ICMS = false;
      this.documento.ISS = false;


    }
  }

  carregarCamposByTipoEmpresa(e) {
    var cdEnquadramentoFiscal = e.target.value;
    this.gerenciarCamposByTipoEmpresa(cdEnquadramentoFiscal);
  }

  isentoIe(event) {
    if (event.target.checked) {
      $("#inscricao-estadual").prop("disabled", true);
    }
    else {
      $("#inscricao-estadual").prop("disabled", false);

    }
  }

  carregarEstados() {
    this.httpClient.get(environment.API_ENDPOINT + "/commons/estados").subscribe((results) => {
      this.estados = results;
      $(".spinner").hide();
      $(".darken-bkg").hide();
    }, (err) => {
      console.log(err);
    })
  }

  carregarCidadesTrigger(event) {

    $(".spinner").show();
    $(".darken-bkg").show();

    var cdEstado = event.target.value;

    this.carregarCidades(cdEstado);

  }

  carregarCidades(cdEstado) {
    this.httpClient.get(environment.API_ENDPOINT + "/commons/cidades/" + cdEstado).subscribe((results) => {
      this.cidades = results;
      $(".spinner").hide();
      $(".darken-bkg").hide();
    }, (err) => {
      console.log(err);
      $(".spinner").hide();
      $(".darken-bkg").hide();
    })
  }

  salvar(event) {
    event.preventDefault();

    console.log(this.form.value);

    $(".spinner").show();
    $(".darken-bkg").show();

    var data = {
      empresaDto: this.form.value,
      key: environment.KEY
    }

    data.empresaDto.itensParcelamento = this.itensParcelamento;

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/salvar", data).subscribe((results) => {
      console.log(results);

      $(window).scrollTop(0);
      $(".alert-success").show();
      $(".spinner").hide();
      $(".darken-bkg").hide();

      setTimeout(() => {
        $(".alert-success").hide();
      }, 3000)

    }, (err) => {
      console.log(err);
      $(window).scrollTop(0);
      $(".spinner").hide();
      $(".darken-bkg").hide();
      $(".alert-danger").show();
      setTimeout(() => {
        $(".alert-danger").hide();
      }, 3000)
    })


  }

  carregarEmpresa() {
    this.route.queryParams.subscribe(params => {
      this.id = params['id'];
      if (!this.id) {
        this.carregarEstados();
        $(".spinner").fadeOut();
        $(".darken-bkg").fadeOut();
      }
      else {
        this.carregarDadosEmpresa(this.id);
      }
    })
  }

  carregarDadosEmpresa(cdEmpresa) {
    this.httpClient.get(environment.API_ENDPOINT + "/empresa/detalhe?cdEmpresa=" + cdEmpresa 
    + "&key=" + environment.KEY).subscribe((results) => {

      this.httpClient.get(environment.API_ENDPOINT + "/empresa/listar/parcelas/" + cdEmpresa).subscribe((parcelas) => {

        this.empresa = results;

        this.carregarEstados();

        this.form.patchValue({
          cdEmpresa: results[0]["cdEmpresa"],
          nmEmpresa: results[0]["nmEmpresa"],
          nmRazaoSocial: results[0]["nmRazaoSocial"],
          nrCnpj: results[0]["nrDocumento"],
          nrInscricaoEstadual: results[0]["nrInscricaoEstadual"],
          cdEstado: results[0]["cdEstado"],
          cdCidade: results[0]["cdCidade"],
          nrInscricaoMunicipal: results[0]["nrInscricaoMunicipal"],
          cdEnquadramentoFiscal: results[0]["cdEnquadramentoFiscal"],
          dtValCertificadoDigital: results[0]["dtValCertificadoDigital"],
          dtValAlvaraBombeiro: results[0]["dtValAlvaraBombeiro"],
          dtValAlvaraPrefeitura: results[0]["dtValAlvaraPrefeitura"],
          dtValAlvaraSanitario: results[0]["dtValAlvaraSanitario"],
          dtValCertReceita: results[0]["dtValCertReceita"],
          dtValCertIcms: results[0]["dtValCertIcms"],
          dtValCertFgts: results[0]["dtValCertFgts"],
          dtValCertPrefeitura: results[0]["dtValCertPrefeitura"],
          dtValCertProcuradoriaIcms: results[0]["dtValCertProcuradoriaIcms"],
          dtValEnquadramentoEirelli: results[0]["dtValEnquadramentoEirelli"],
          dtJucesp: results[0]["dtJucesp"],
          dsObservacaoJucesp: results[0]["dsObservacaoJucesp"],
          dsTipoCertificadoDigital: results[0]["dsTipoCertificadoDigital"],
          dsTipoDocumento: results[0]["dsTipoDocumento"]
        })

        if (!results[0]["nrInscricaoEstadual"]) {
          $("#isentoIe").attr("checked", "true");
          $("#inscricao-estadual").attr("disabled", "true");
        }

        this.carregarCidades(results[0]["cdEstado"]);
        this.gerenciarCamposByTipoEmpresa(results[0].cdEnquadramentoFiscal);

        console.log(results[0]["fgIsIrECsllTrimestral"]);

        setTimeout(() => {
          this.form.patchValue({
            fgIsIrECsllTrimestral: results[0]["fgIsIrECsllTrimestral"]
          })
          $(".spinner").fadeOut();
          $(".darken-bkg").fadeOut();
        }, 500);
      })

    })
  }

  mudarStatusEmpresa(event) {

    $(".spinner").show();
    $(".darken-bkg").show();

    var newStatus = event.target.value;

    console.log(newStatus);

    this.httpClient.get(environment.API_ENDPOINT + "/empresa/status/" + this.empresa[0].cdEmpresa + "?key=" + environment.KEY + "&newStatus=" + newStatus).subscribe((results) => {
      console.log(results);
      $(window).scrollTop(0);
      $(".alert-success").show();
      $(".spinner").hide();
      $(".darken-bkg").hide();


      setTimeout(() => {
        $(".alert-success").hide();
      }, 3000)

    }, (fail) => {
      console.log(fail);
      $(window).scrollTop(0);
      $(".spinner").hide();
      $(".darken-bkg").hide();
      $(".alert-danger").show();
      setTimeout(() => {
        $(".alert-danger").hide();
      }, 3000)
    })

  }

  validaPerfil(cdTipo) {

    if (cdTipo != 1) {
      setTimeout(() => {
        $('.usuario-menu').remove();

      }, 100);
    }
  }

  adicionarParcelamento() {
    let parcelamento = $('#parcelamentoSelect').val();

    let parcelamentoDto = {
      dsDocumento: parcelamento,
      qtdParcelas: null
    }

    this.itensParcelamento.push(parcelamentoDto);

  }

  adicionarQtdParcelas(dsDocumento, qtdeParcelas) {

    console.log(qtdeParcelas);

    let index = this.itensParcelamento.findIndex((object) => {
      return object.dsDocumento == dsDocumento;
    });

    console.log(index);

    this.itensParcelamento[index].qtdeParcelas = qtdeParcelas;

    console.log(this.itensParcelamento);
  }

  excluirParcelamento(dsDocumento) {
    console.log(dsDocumento);
    this.itensParcelamento = this.itensParcelamento.filter((object) => {
      return object.dsDocumento != dsDocumento;
    })
    console.log(this.itensParcelamento);
  }

}
