import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { navItems } from '../../_nav';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import * as $ from 'jquery';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-empresa-consulta',
  templateUrl: './empresa-consulta.component.html',
  styleUrls: ['./empresa-consulta.component.scss']
})
export class EmpresaConsultaComponent implements OnInit {

  public data: any;

  public page = 1;
  public pageSize = 10;

  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;

  public enquadramentos: any;

  public form: FormGroup;
  public iniciarProcessoForm: FormGroup;

  public empresas: any = [];

  constructor(@Inject(DOCUMENT) _document?: any, public router?: Router, public httpClient?: HttpClient, public formBuilder?: FormBuilder) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ['class']
    });
  }

  @ViewChild('successModal') public successModal: ModalDirective;

  ngOnDestroy(): void {
    this.changes.disconnect();
  }

  ngOnInit() {

    $(".spinner").show();
    $(".darken-bkg").show()

    this.form = this.formBuilder.group({
      nmEmpresa: '',
      nrCnpj: '',
      cdEnquadramentoFiscal: '',
      stStatus: 1
    })

    this.iniciarProcessoForm = this.formBuilder.group({
      cdEmpresa: null,
      dtInicioProcesso: '',
      cdIniciadoPor: ''
    })

    this.data = this.retrieveStoredData();
    console.log(this.data);
    if (!this.data) {
      this.router.navigateByUrl("/403");
    }

    this.validaPerfil(this.data["cdTipo"]);

    this.carregarEnquadramentoFiscal();
  }

  retrieveStoredData() {
    return JSON.parse(localStorage.getItem("userData"));
  }

  logout() {
    localStorage.clear();
    this.router.navigateByUrl("/");
  }

  carregarEnquadramentoFiscal() {
    this.httpClient.get(environment.API_ENDPOINT + "/empresa/enquadramento").subscribe((data) => {
      this.enquadramentos = data;
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

    })
  }

  listarEmpresas() {

    $(".spinner").show();
    $(".darken-bkg").show()

    var data = {
      empresaFiltroDto: this.form.value,
      key: environment.KEY
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/listar", data).subscribe((results) => {
      this.empresas = results;
      $(".spinner").hide();
      $(".darken-bkg").hide()
    }, (err) => {
      console.log(err)
      $(".spinner").hide();
      $(".darken-bkg").hide()
    })
  }

  abrirModal(cdEmpresa, nmEmpresa) {
    $("#modalEmpresa").html(nmEmpresa);
    this.iniciarProcessoForm.patchValue({
      cdEmpresa: cdEmpresa,
      cdIniciadoPor: this.data.cdLogin
    })
    this.successModal.show();
  }

  iniciarProcesso() {

    $(".spinner").show();
    $(".darken-bkg").show();

    var data = {
      key: environment.KEY,
      inicioProcessoDto: this.iniciarProcessoForm.value
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/processo/iniciar", data).subscribe((results) => {

      console.log(results);
      this.successModal.hide();
      $(".spinner").hide();
      $(".darken-bkg").hide();
      $(window).scrollTop(0);
      $(".alert-success").show();

      setTimeout(() => {
        $(".alert-success").fadeOut();

      }, 5000)
    }, (fail) => {

      if (fail["error"] == "J existem processos em andamento para esse perodo.") {
        $(".alert-danger").html("J existem processos em andamento para esse perodo.");
      }
      else if (fail["error"] == "Data de incio invlida") {
        $(".alert-danger").html("Data de incio invlida");
      }
      else {
        $(".alert-danger").html("Ocorreu um erro inesperado, tente novamente aps alguns instantes.");
      }


      console.log(fail);
      this.successModal.hide();
      $(".spinner").hide();
      $(".darken-bkg").hide();
      $(window).scrollTop(0);
      $(".alert-danger").show();

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 5000)

    })
  }
  validaPerfil(cdTipo) {
    
    if (cdTipo != 1) {

      console.log("NAO EH ADMIN");
      setTimeout(() => {
        $(".usuario-menu").remove();
        $(".spinner").fadeOut();
        $(".darken-bkg").fadeOut();
      }, 100);
    }
    else {
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
    }
  }
}
