import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { navItems } from '../../_nav';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import * as $ from 'jquery';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { PerfilEnum } from '../../perfil.enum';

@Component({
  selector: 'app-empresa-processos-consulta',
  templateUrl: './empresa-processos-consulta.component.html',
  styleUrls: ['./empresa-processos-consulta.component.scss']
})
export class EmpresaProcessosConsultaComponent implements OnInit {

  public data: any;

  public Perfil = PerfilEnum;

  public page = 1;
  public pageSize = 10;

  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;

  public enquadramentos: any;

  public form: FormGroup;

  public processos: any;

  public empresaProcessos: any = [];
  public processosConsulta: any;

  public listaDeProcessos = [];

  public checkAllControl = false;

  constructor(@Inject(DOCUMENT) _document?: any, public router?: Router, public httpClient?: HttpClient, public formBuilder?: FormBuilder) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ['class']
    });
  }

  @ViewChild('themeModal') public themeModal: ModalDirective;


  ngOnDestroy(): void {
    this.changes.disconnect();
  }

  ngOnInit() {
    this.carregarProcessos();
    this.carregarEnquadramentoFiscal();
    this.form = this.formBuilder.group({
      cdEtapaProcesso: null,
      nmEmpresa: null,
      cdProcesso: null,
      cdEnquadramentoFiscal: null,
      cdStatus: 0,
      dtPeriodoDe: null,
      dtPeriodoAte: null
    })

    this.data = this.retrieveStoredData();
    console.log(this.data);
    if (!this.data) {
      this.router.navigateByUrl("/403");
    }

    this.validaPerfil(this.data["cdTipo"]);
  }

  retrieveStoredData() {
    return JSON.parse(localStorage.getItem("userData"));
  }

  logout() {
    localStorage.clear();
    this.router.navigateByUrl("/");
  }

  carregarEnquadramentoFiscal() {
    this.httpClient.get(environment.API_ENDPOINT + "/empresa/enquadramento").subscribe((data) => {
      this.enquadramentos = data;
    })
  }


  carregarProcessos() {
    this.httpClient.get(environment.API_ENDPOINT + "/empresa/processo/listar/todos").subscribe((results) => {
      this.processos = results;
    }, (err) => {
      console.log(err);
    })
  }

  listarProcessos() {

    $(".checkAll").attr("checked", "false");
    $(".checkAll").removeAttr("checked");

    console.log(this.checkAllControl)

    $(".darken-bkg").show();
    $(".spinner").show();

    var data = {
      key: environment.KEY,
      processoFiltroDto: this.form.value
    }

    console.log(this.form.value);

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/listar/processos", data).subscribe((results) => {
      this.empresaProcessos = results;
      console.log(results);
      $(".darken-bkg").fadeOut();
      $(".spinner").fadeOut();
    }, (err) => {
      console.log(err);
      $(window).scrollTop(0);
      $(".alert-danger").show();

      $(".darken-bkg").fadeOut();
      $(".spinner").fadeOut();

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 5000)


    })
  }

  adicionarItem(e) {

    var cdEtapaProcesso = e.target.value;

    if (e.target.checked) {
      this.adicionarItemNoArray(cdEtapaProcesso);
    }
    else {
      this.removeItemNoArray(cdEtapaProcesso);
    }
  }

  adicionarItemNoArray(cdEtapaProcesso) {
    this.listaDeProcessos.push(cdEtapaProcesso);
  }

  removeItemNoArray(cdEtapaProcesso) {
    this.listaDeProcessos = this.listaDeProcessos.filter((value) => {
      return value != cdEtapaProcesso;
    })
  }

  finalizarAvulso(e) {

    $(".spinner").show();
    $(".darken-bkg").show();

    var cdEtapaProcesso = e.target.value;

    var finalizarProcessoDto = {
      cdLogin: this.data.cdLogin,
      cdEtapaProcesso: cdEtapaProcesso
    }

    var data = {
      key: environment.KEY,
      finalizarProcessoDto: finalizarProcessoDto
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/finalizar", data).subscribe((results) => {

      $(window).scrollTop(0);

      $(".alert-success").show();

      console.log(results);
      this.listarProcessos();

      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-success").fadeOut();
      }, 5000)

    }, (err) => {

      $(window).scrollTop(0);

      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      $(".alert-danger").show();
      console.log(err);

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 5000)

    })

  }

  finalizarEmLote() {

    if (this.listaDeProcessos.length > 0) {
      $(".spinner").show();
      $(".darken-bkg").show();

      var finalizarProcessoLoteDto = {
        cdLogin: this.data.cdLogin,
        processosList: this.listaDeProcessos
      }

      var data = {
        key: environment.KEY,
        finalizarProcessoLoteDto: finalizarProcessoLoteDto
      }

      this.httpClient.post(environment.API_ENDPOINT + "/empresa/finalizar/lote", data).subscribe((results) => {

        $(".spinner").hide();
        $(".darken-bkg").hide();

        $(window).scrollTop(0);

        $(".alert-success").show();

        this.listaDeProcessos = [];
        $(".checkbox-control").removeAttr("checked");


        console.log(results);
        this.listarProcessos();

        setTimeout(() => {
          $(".alert-success").fadeOut();
        }, 5000)

      }, (err) => {

        $(".spinner").hide();
        $(".darken-bkg").hide();

        $(window).scrollTop(0);

        $(".alert-danger").show();
        console.log(err);

        this.listaDeProcessos = [];
        $(".checkbox-control").removeAttr("checked");

        this.listarProcessos();

        setTimeout(() => {
          $(".alert-danger").fadeOut();
        }, 5000)

      })

    }
    else {

      $(window).scrollTop(0);

      $(".alert-danger").show();

      this.listaDeProcessos = [];
      $(".checkbox-control").removeAttr("checked");


      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 5000);


    }

  }


  consultarDetalhesProcesso(cdEtapaProcesso) {

    var data = {
      key: environment.KEY,
      processoFiltroDto: {
        cdEtapaProcesso: cdEtapaProcesso
      }
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/listar/processos", data).subscribe((results) => {
      console.log(results);
      this.themeModal.show();
      this.processosConsulta = results;
    })
  }
 


  checkAll(event) {

    if (event.target.checked) {
      $("input[type='checkbox']:not(:checked)").attr("checked", "true");
      $(".checkbox-control").each((i, checkbox) => {
        if(!checkbox["disabled"]){
          this.adicionarItemNoArray(checkbox["value"]);
        }
      })
    }
    else {
      console.log("not checked");
      $(".checkbox-control").removeAttr("checked");
      $(".checkbox-control").each((i, checkbox) => {
        this.removeItemNoArray(checkbox["value"]);
      })
    }
  }
  
  validaPerfil(cdTipo) {

    if (cdTipo != 1) {

      console.log("NAO EH ADMIN");
      setTimeout(() => {
        $(".usuario-menu").remove();
        $(".spinner").fadeOut();
        $(".darken-bkg").fadeOut();
      }, 100);
    }
    else {
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
    }
  }



}
