import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { navItems } from '../../_nav';
import { environment } from '../../../environments/environment';
import * as $ from "jquery";

@Component({
  selector: 'app-usuario-consulta',
  templateUrl: './usuario-consulta.component.html',
  styleUrls: ['./usuario-consulta.component.scss']
})
export class UsuarioConsultaComponent implements OnInit {

  public data: any;

  public usuarios: any = [];

  public page = 1;
  public pageSize = 5;

  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;

  public enquadramentos: any;

  constructor(@Inject(DOCUMENT) _document?: any, public router?: Router, public httpClient?: HttpClient) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ['class']
    });
  }

  ngOnDestroy(): void {
    this.changes.disconnect();
  }

  ngOnInit() {
    this.data = this.retrieveStoredData();
    this.validaPerfil(this.data["cdTipo"]);
    if (!this.data) {
      this.router.navigateByUrl("/403");
    }
  }

  retrieveStoredData() {
    return JSON.parse(localStorage.getItem("userData"));
  }

  logout() {
    localStorage.clear();
    this.router.navigateByUrl("/");
  }

  listarUsuarios() {

    $(".spinner").show();
    $(".darken-bkg").show();

    var nmUsuario = $("#usuario").val();

    this.httpClient.get(environment.API_ENDPOINT + "/usuario/listar?nmUsuario=" + nmUsuario + "&key=" + environment.KEY).subscribe((data) => {
      this.usuarios = data;
      console.log(this.usuarios)
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
    })
  }

  validaPerfil(cdTipo) {
    if (cdTipo != 1) {
      window.location.href = "/#/403";
    }
  }

}
