import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { navItems } from '../../_nav';
import { environment } from '../../../environments/environment';
import * as $ from 'jquery';
import { FormBuilder, FormGroup } from '@angular/forms';
import { analyzeAndValidateNgModules } from '@angular/compiler';

@Component({
  selector: 'app-empresa-processos-detalhe',
  templateUrl: './empresa-processos-detalhe.component.html',
  styleUrls: ['./empresa-processos-detalhe.component.scss']
})
export class EmpresaProcessosDetalheComponent implements OnDestroy {

  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;

  public data: any;
  public cdEmpresa: any;
  public dtReferencia: any;
  public documentos: any;
  public ultimaEtapa = {
    nmEmpresa: '...',
    dsEnquadramentoFiscal: '...',
    dsMesReferencia: '..-..',
    dsSiglaProcesso: '...',
    iniciadoPor: '...',
    dsProgresso: '...',
    fgFinalizado: null
  }

  public legalizacao: any = [
    {
      dsCorDtJucesp: null,
      dsObservacaoJucesp: ''
    }
  ];

  public itensParcelamento: any;

  public observacoesForm: any;

  public parcelasArray: any = [];

  constructor(@Inject(DOCUMENT) _document?: any, public router?: Router, public httpClient?: HttpClient, public formBuider?: FormBuilder, public route?: ActivatedRoute) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ['class']
    });
  }


  ngOnDestroy(): void {
    this.changes.disconnect();
  }


  ngOnInit() {

    $(".spinner").show();
    $(".darken-bkg").show();

    this.observacoesForm = this.formBuider.group({
      observacoesField: '',
      caixaMensalField: ''
    })


    this.data = this.retrieveStoredData();
    console.log(this.data);
    if (!this.data) {
      this.router.navigateByUrl("/403");
    }

    this.validaPerfil(this.data["cdTipo"]);

    this.route.queryParams.subscribe(params => {
      this.cdEmpresa = params['cdEmpresa'];
      this.dtReferencia = params['dtReferencia'];
      this.carregarDadosEtapaAtual();
      this.carregarDadosLegalizacao();
    })

  }

  retrieveStoredData() {
    return JSON.parse(localStorage.getItem("userData"));
  }

  carregarDocumentos(cdEmpresa, dtReferencia) {

    var empresaProcessoDetalheFiltro = {
      cdEmpresa: cdEmpresa,
      dtPeriodoDe: dtReferencia,
      dtPeriodoAte: dtReferencia
    }

    var data = {
      empresaProcessoDetalheFiltro: empresaProcessoDetalheFiltro,
      key: environment.KEY
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/processo/detalhar", data).subscribe((results) => {
      this.httpClient.get(environment.API_ENDPOINT + "/empresa/listar/parcelas/" + cdEmpresa).subscribe((parcelas) => {

        this.parcelasArray = parcelas;

        this.parcelasArray.forEach((item, i) => {
          if (item.indicador.qtdParcelasTotais == null) {
            if (item.indicador.qtdParcelasPendentes == null) {
              item.indicador.qtdParcelasTotais = item.indicador.qtdParcelasPagas;
            }
            else {
              item.indicador.qtdParcelasTotais = item.indicador.qtdParcelasPendentes;
            }
          }
          if (item.indicador.qtdParcelasPagas == null) {
            item.indicador.qtdParcelasPagas = 0;
          }
        })

        this.itensParcelamento = parcelas;



        console.log(parcelas);

        console.log(results);
        this.documentos = results;

        var i = 0;

        if (results["length"] > 0) {

          for (i = 0; i < results["length"]; i++) {
            if (results[i].dsObservacaoProcesso != null) {
              this.observacoesForm.patchValue({
                observacoesField: results[i].dsObservacaoProcesso,
                caixaMensalField: results[i].vrCaixaMensal
              })
              break;

            }
          }

        }

        $(".spinner").hide();
        $(".darken-bkg").hide();

      })

    })

  }

  carregarDadosEtapaAtual() {


    this.httpClient.get(environment.API_ENDPOINT + "/empresa/consulta/ultimaetapa/" + this.cdEmpresa + "?dsMesReferencia=" + this.dtReferencia).subscribe((results) => {
      console.log(results);
      var data = {
        key: environment.KEY,
        processoFiltroDto: {
          cdProcesso: results[0]["cdProcesso"],
          cdEmpresa: this.cdEmpresa
        }
      }

      this.httpClient.post(environment.API_ENDPOINT + "/empresa/listar/processos", data).subscribe((dados) => {
        console.log(dados);

        this.ultimaEtapa = dados[0];

        this.carregarDocumentos(this.cdEmpresa, this.dtReferencia);

      }, (error) => {
        console.log(error);
        $(".spinner").hide();
        $(".darken-bkg").hide();
      })
    }, (error) => {
      console.log(error);
      $(".spinner").hide();
      $(".darken-bkg").hide();
    })
  }

  finalizarDocumento(cdEmpresa, cdEtapaProcesso, cdDocumento, dtValidade) {

    $(".spinner").show();
    $(".darken-bkg").show();

    var finalizarDocumentoDto = {
      cdEmpresa: cdEmpresa,
      cdEtapaProcesso: cdEtapaProcesso,
      cdDocumento: cdDocumento,
      dtValidade: dtValidade

    }

    var data = {
      finalizarDocumentoDto: finalizarDocumentoDto,
      key: environment.KEY
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/finalizar/documento", data).subscribe((results) => {
      console.log(results);
      this.carregarDocumentos(this.cdEmpresa, this.dtReferencia);
      $(window).scrollTop(0);
      $(".alert-success").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-success").fadeOut();
      }, 3000);
    }, (err) => {
      console.log(err);
      $(".alert-danger").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 3000);
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
    })
  }

  logout() {
    localStorage.clear();
    this.router.navigateByUrl("/");
  }

  salvarObservacoes() {

    $(".spinner").show();
    $(".darken-bkg").show();

    var data = {
      key: environment.KEY,
      cdEmpresa: this.cdEmpresa,
      dsMesReferencia: this.dtReferencia,
      dsObservacao: this.observacoesForm.value.observacoesField,
      vrCaixaMensal: this.observacoesForm.value.caixaMensalField
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/processo/observacoes/atualizar", data).subscribe((results) => {
      console.log(results);

      $(window).scrollTop(0);
      $(".alert-success").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-success").fadeOut();
      }, 3000);

    }, (fail) => {
      $(window).scrollTop(0);
      $(".alert-danger").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 3000);
      console.log(fail);
    })
  }
  validaPerfil(cdTipo) {

    if (cdTipo != 1) {

      console.log("NAO EH ADMIN");
      setTimeout(() => {
        $(".usuario-menu").remove();

      }, 100);
    }
  }

  finalizarParcela(cdParcelaParcelamento) {

    $(".spinner").show();
    $(".darken-bkg").show();


    this.httpClient.get(environment.API_ENDPOINT + "/empresa/finalizar/parcela/" + cdParcelaParcelamento).subscribe((results) => {
      console.log(results);
      $(window).scrollTop(0);
      $(".alert-success").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-success").fadeOut();
      }, 3000);

      this.carregarDocumentos(this.cdEmpresa, this.dtReferencia);

    }, (fail) => {
      $(window).scrollTop(0);
      $(".alert-danger").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 3000);
      console.log(fail);
    })
  }

  carregarDadosLegalizacao() {
    this.httpClient.get(environment.API_ENDPOINT + "/empresa/obrigacoes/legalizacao/" + this.cdEmpresa).subscribe((results) => {
      this.legalizacao = results;
    })
  }

}
