import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, FormGroup } from '@angular/forms';
import { environment } from '../../../environments/environment';
import * as $ from 'jquery';

@Component({
  selector: 'app-dashboard',
  templateUrl: 'login.component.html'
})
export class LoginComponent implements OnInit{

  public form: FormGroup;

  ngOnInit() {
    this.form = this.formBuilder.group({
      username: '',
      password: ''
    })

    if (localStorage.getItem('userData')) {
      this.router.navigateByUrl('/dashboard');
    }
  }

  constructor(public router: Router, public httpClient: HttpClient, public formBuilder: FormBuilder) { }

  login() {

    $('.darken-bkg').show();
    $('.spinner').show();

    if (!this.form.value.username || !this.form.value.password) {

      $('.darken-bkg').hide();
      $('.spinner').hide();
      $('#error').html('Usurio ou senha esto vazios!');
      $('.error').show();

      setTimeout(() => {
        $('.error').fadeOut();
      }, 3000);

    }
    else {

      var data = {
        credentials: {
          username: this.form.value.username,
          password: this.form.value.password,
        },
        key: environment.KEY
      }

      this.httpClient.post(environment.API_ENDPOINT + '/usuario/login', data).subscribe((results) => {
        console.log(results);

        localStorage.setItem('userData', JSON.stringify(results));

        $('.darken-bkg').hide();
        $('.spinner').hide();
        this.router.navigateByUrl('/dashboard');
      }, (err) => {
        console.log(err.error);
        $('#error').html(err.error);
        $('.error').show();
        setTimeout(() => {
          $('.error').fadeOut();
        }, 3000);
        $('.darken-bkg').hide();
        $('.spinner').hide();
      })
    }


  }

}

