import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { navItems } from '../../_nav';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import * as $ from 'jquery';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { PerfilEnum } from '../../perfil.enum';
@Component({
  selector: 'app-documento-consulta',
  templateUrl: './documento-consulta.component.html',
  styleUrls: ['./documento-consulta.component.scss']
})
export class DocumentoConsultaComponent implements OnInit {
  public data: any;

  public Perfil = PerfilEnum;

  public page = 1;
  public pageSize = 10;

  public navItems = navItems;
  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement;

  public form: FormGroup;
  public documentosTipo: any;

  public documentos: any = [];
  public listaDeDocumentos: any = [];

  constructor(@Inject(DOCUMENT) _document?: any, public router?: Router, public httpClient?: HttpClient, public formBuilder?: FormBuilder) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
    });
    this.element = _document.body;
    this.changes.observe(<Element>this.element, {
      attributes: true,
      attributeFilter: ['class']
    });
  }

  @ViewChild('themeModal') public themeModal: ModalDirective;


  ngOnDestroy(): void {
    this.changes.disconnect();
  }

  ngOnInit() {
    this.listarTiposDocumento();
    this.form = this.formBuilder.group({
      nmEmpresa: null,
      dtPeriodoDe: null,
      dtPeriodoAte: null,
      cdDocumento: null,
      cdStatusDocumento: null,
      dtPeriodoValidadeDe: null,
      dtPeriodoValidadeAte: null,

    })

    this.data = this.retrieveStoredData();
    console.log(this.data);
    if (!this.data) {
      this.router.navigateByUrl("/403");
    }

    this.validaPerfil(this.data["cdTipo"]);
  }

  retrieveStoredData() {
    return JSON.parse(localStorage.getItem("userData"));
  }

  logout() {
    localStorage.clear();
    this.router.navigateByUrl("/");
  }



  // removeItemNoArray(cdEtapaProcesso) {
  //   this.listaDeProcessos = this.listaDeProcessos.filter((value) => {
  //     return value != cdEtapaProcesso;
  //   })
  // }

  carregarDocumentos() {

    $(".spinner").show();
    $(".darken-bkg").show();


    var data = {
      key: environment.KEY,
      documentosFiltroDto: this.form.value

    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/documentos/listar", data).subscribe((results) => {
      this.documentos = results;
      $(window).scrollTop(0);
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
    }, (fail) => {
      console.log(fail);
      $(".alert-danger").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 3000);
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
    })
  }

  validaPerfil(cdTipo) {

    if (cdTipo != 1) {

      console.log("NAO EH ADMIN");
      setTimeout(() => {
        $(".usuario-menu").remove();
        $(".spinner").fadeOut();
        $(".darken-bkg").fadeOut();
      }, 100);
    }
    else {
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
    }
  }

  listarTiposDocumento() {
    this.httpClient.get(environment.API_ENDPOINT + "/empresa/documentos/tipos/listar").subscribe((results) => {
      this.documentosTipo = results;
      console.log(results);
    })
  }

  adicionarItem(e, cdEmpresa, cdEtapaProcesso, cdDocumento, dtValidade) {

    if (e.target.checked) {
      this.listaDeDocumentos.push({
        cdEmpresa: cdEmpresa,
        cdEtapaProcesso: cdEtapaProcesso,
        cdDocumento: cdDocumento,
        dtValidade: dtValidade
      })
    }
    else {
      this.listaDeDocumentos = this.listaDeDocumentos.filter((item) => {
        if (item.cdEmpresa == cdEmpresa && item.cdEtapaProcesso == cdEtapaProcesso && item.cdDocumento == cdDocumento && item.dtValidade == dtValidade) {
          console.log("ok");
        }
        else {
          return item;
        }
      })
    }

    console.log(this.listaDeDocumentos);

  }

  finalizarEmLote(){

    $(".spinner").show();
    $(".darken-bkg").show();

    var data = {
      key: environment.KEY,
      listaDeDocumentos: this.listaDeDocumentos
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/documento/finalizar/lote",data).subscribe((results)=>{
      console.log(results);
      $(window).scrollTop(0);
      $(".alert-success").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
      
      setTimeout(() => {
        $(".alert-success").fadeOut();
      }, 3000);
      this.carregarDocumentos();
      this.listaDeDocumentos = [];
    },(fail)=>{
      console.log(fail);
      $(".alert-danger").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 3000);
    })
  }

  finalizarDocumento(cdEmpresa, cdEtapaProcesso, cdDocumento, dtValidade) {

    $(".spinner").show();
    $(".darken-bkg").show();

    var finalizarDocumentoDto = {
      cdEmpresa: cdEmpresa,
      cdEtapaProcesso: cdEtapaProcesso,
      cdDocumento: cdDocumento,
      dtValidade: dtValidade

    }

    var data = {
      finalizarDocumentoDto: finalizarDocumentoDto,
      key: environment.KEY
    }

    this.httpClient.post(environment.API_ENDPOINT + "/empresa/finalizar/documento", data).subscribe((results) => {
      console.log(results);
      this.carregarDocumentos();
      $(window).scrollTop(0);
      $(".alert-success").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-success").fadeOut();
      }, 3000);
    }, (err) => {
      console.log(err);
      $(".alert-danger").show();
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();

      setTimeout(() => {
        $(".alert-danger").fadeOut();
      }, 3000);
      $(".spinner").fadeOut();
      $(".darken-bkg").fadeOut();
    })
  }

}
